export default {
  props: ['data', 'display'],
  data () {
    return {

    }
  },
  methods: {
    touch () {
      alert('touch')
    },
    liked () {
      this.$emit('onLike')
    },
    share () {
      this.$emit('onShare')
    },
    ds () {
      this.setUserInfo()
      this.$emit('onDs')
    },
    bp () {
      this.setUserInfo()
      this.$emit('onBp')
    },
    setUserInfo () {
      this.$store.commit('main/SET_CURRENT_USER_INFO', this.data)
    }
  }
}



